import React from 'react';
import { View } from 'react-native';
import { Title, Button } from '../../styles/styledComponents';
import R from '../../styles/R';

//= ==============================================================================================
const Btn = ({ backgroundColor, onSubmit, label, padding, font, disabled }) => (
    <View style={{ justifyContent: 'center', alignItems: 'center' }}>
        <Button
            disabled={disabled}
            backgroundColor={backgroundColor}
            activeOpacity={0.7}
            padding={padding}
            height="60px"
            border="12px"
            onPress={() => onSubmit()}>
            <Title font={font} weight="bold" color={R.colors.white}>
                {label}
            </Title>
        </Button>
    </View>
);

export default Btn;
